:global {
  $main-bg-color: #ffffff;
  $secondary-bg-color: #2f303a;
  $third-bg-color: #f5f4fa;
  $main-text-color: #757575;
  $secondary-text-color: #212121;
  $accent-color: #2196f3;
  $third-text-color: #000000;
  $main-font: 'Roboto', sans-serif;
  $border-color: #eeeeee;
  $social-icon-color: #afb1b8;

  // breakpoints

  $first-bp: 480px;
  $second-bp: 768px;
  $third-bp: 1200px;

  body {
    box-sizing: border-box;

    background-color: $main-bg-color;
    color: $main-text-color;
    font-family: $main-font;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.03em;
    line-height: 1.71;
    margin: 0;
  }
  body.modal-open {
    box-sizing: border-box;
    overflow: hidden;
  }
  .link {
    text-decoration: none;
  }
  .link:hover,
  .link:focus {
    color: $accent-color;
  }
  /* Забрали маркери в списках */
  .list {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  /* Забрали відступи в заголовках */
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin: 0;
  }
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  ul,
  ol {
    margin-top: 0px;
    margin-bottom: 0px;
    padding-left: 0px;
  }
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
    @media screen and (min-width: $third-bp) {
      padding-top: 94px;
      padding-bottom: 94px;
    }
  }
  a {
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
  .transition {
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }

  .conteiner {
    padding-left: 15px;
    padding-right: 15px;

    @media screen and (min-width: 481px) {
      width: 480px;
      margin: 0 auto;
    }
    @media screen and (min-width: 768px) {
      width: 768px;
    }
    @media screen and (min-width: 1200px) {
      width: 1200px;
    }
  }
}
